.cont2{position:relative;top:3rem;opacity:0;max-width:1200px;min-width:600px;margin:0 auto;font-family:"Noto Sans KR",sans-serif}.cont2-txt{margin-top:5rem}.cont2-txt h2{padding-bottom:20px}.cont2-list{margin:5rem 0}.cont2-li{width:100%}.cont2-li a{display:flex;gap:1rem;justify-content:space-between;align-items:center;border:1px solid #f3f3f3;margin:3rem 0;box-shadow:5px 5px 30px rgba(0,0,0,0)}.cont2-li a:hover{box-shadow:5px 5px 30px rgba(0,0,0,.09);transition:.3s ease-out}.cont2-li a .cont2-imgbox{width:192px;aspect-ratio:192/60}.cont2-li a .cont2-imgbox img{display:block;width:100%;height:100%;opacity:.5;transition:.3s ease-out}.cont2-list-img{width:480px}.cont2-list-img img{width:100%;height:100%}.desc-list{flex:1;color:#111;font-size:1.625em;letter-spacing:-0.75px;font-weight:300}.desc-list>*{margin-left:2rem}.desc-list li::before{content:" · "}.desc-list h3{margin-bottom:1rem;font-size:2rem}@media screen and (max-width: 1200px){.cont2{width:90%;height:auto}.cont2-list{display:grid;grid-template-columns:repeat(2, 1fr);gap:50px}.cont2-list .cont2-li .cont2-list-img{width:100%}.cont2-list .cont2-li a{flex-direction:column;flex-wrap:wrap;height:100%;margin:0}.cont2-list .cont2-li .desc-list{width:100%}.cont2-list .cont2-li .cont2-imgbox{margin:20px 0}}@media screen and (max-width: 950px){.desc-list{font-size:1.525em}}@media screen and (max-width: 900px){.cont2-txt .core-p{font-size:1.5rem}.cont2-list{grid-template-columns:repeat(1, 1fr)}}@media screen and (max-width: 600px){.cont2{min-width:auto}}